import React, { Component } from 'react'
import {Table, tags } from "antd"
import "antd/dist/antd.css"
class TableA extends Component {
    render() { 
        const columns = [
            {
              title: 'Name',
              dataIndex: 'name',
            },
            {
              title: 'Age',
              dataIndex: 'age',
              defaultSortOrder: 'descend',
              sorter: (a, b) => a.age - b.age,         
            },
            {
              title: "Year",
              dataIndex: 'yr',  
            },
            {
                title: 'Dept',
                dataIndex: 'dept',

            }
          ];
          
          const data = [
            {
              key: '1',
              name: 'Bhanu Prasad',
              age: 18,
              yr: "2nd",
              dept:"CSE"
            },
            {
                key: '2',
                name: 'Bhanu Prasad',
                age: 19,
                yr: '3rd',
                dept:"CSE"
              },
              {
                key: '3',
                name: 'Bhanu Prasad',
                age: 20,
                yr: '4th',
                dept:"CSE"
              },

              {
                key: '4',
                name: 'Bhanu Prasad',
                age: 17,
                yr: '1st',
                dept:"CSE"
              },
              {
                key: '5',
                name: 'Bhanu Prasad',
                age: 11,
                yr: '0',
                dept:"---"
              },
          ];
          


        return ( <Table columns={columns} dataSource={data} onChange={this.onChange()} className="table table-bordered table-responsive"/> );
    }
    onChange(pagination, filters, sorter, extra) {
        console.log('params', pagination, filters, sorter, extra);
      }
}
 


export default TableA;